@charset "UTF-8";
@import "variables";
@import "../../../sass/func";

.content-feedback {
  padding: pxToRem(32px) pxToRem(30px) pxToRem(60px);

  color: #333;
  section {
    position: relative;
    &:after {
      @include border(bottom, #f1f1f1);
    }
  }
  .sec-top {
    padding-bottom: pxToRem(30px);

    font-size: pxToRem(30px);
    line-height: pxToRem(42px);
  }
  .sec-feedback {
    padding-bottom: pxToRem(40px);
    textarea {
      border: 0;
      padding: pxToRem(24px) 0;
      width: 100%;
      height: pxToRem(167px * 2);

      font-size: pxToRem(28px);
      line-height: pxToRem(42px);

      outline: none;
      &::-webkit-input-placeholder {
        color: #ccc;
      }
    }
    .counting {
      font-size: pxToRem(28px);
      text-align: right;

      color: #666;
    }
    .list-img {
      padding: pxToRem(36px) 0 pxToRem(20px);
      width: pxToRem(720px);

      font-size: 0;
      white-space: nowrap;
      li {
        display: inline-block;

        position: relative;

        margin-right: pxToRem(30px);
        width: pxToRem(75px * 2);
        height: pxToRem(75px * 2);
      }
      img {
        display: block;

        width: 100%;
        height: 100%;
      }
      .icon-del {
        position: absolute;
        top: pxToRem(-30px);
        right: pxToRem(-30px);

        width: pxToRem(60px);
        height: pxToRem(60px);

        background: url("../images/feedback/del.png") no-repeat center;
        background-size: pxToRem(36px) pxToRem(36px);
      }
      .item-add {
        margin-right: 0;

        background: url("../images/feedback/add.png") no-repeat center;
        background-color: #f7f7f7;
        background-size: pxToRem(52px) pxToRem(52px);
        input {
          position: absolute;
          top: 0;
          left: 0;
          z-index: 9;

          width: 100%;
          height: 100%;

          opacity: 0;
        }
      }
    }
    .tip {
      font-size: pxToRem(24px);

      color: #ccc;
    }
  }
  .sec-tel {
    margin-bottom: pxToRem(80px);
    padding-top: pxToRem(46px);

    font-size: pxToRem(28px);
    input {
      width: 100%;
      height: pxToRem(100px);
      &::-webkit-input-placeholder {
        color: #ccc;
      }
    }
  }
}
